import React,{useEffect, useRef, useState} from 'react';
import {Line} from 'react-chartjs-2';
import {IChartData,IDatasetData} from './DataInterfaces';

function produceIChartData(ISymbolData:any):IChartData|any{
    if(ISymbolData.hgh === undefined){
        return {};
    }
    const weekDays:Array<string> = ['Monday','Thursday','Wednesday','Tuesday','Friday'];
    let labels:Array<string> = [];
    for(let index:number = 0; index < ISymbolData.low.length; index++){
        labels.push(weekDays[index%5]);
    }
    let dataCurr:IDatasetData = {label:'Average Prices',data:ISymbolData.curr,fill:true,borderColor:'blue'};
    let dataHgh:IDatasetData = {label:'High Prices',data:ISymbolData.hgh,fill:true,borderColor:'purple'};
    let dataLow:IDatasetData = {label:'Low Prices',data:ISymbolData.low,fill:true,borderColor:'red'};
    let IChartData:IChartData = {labels:labels,datasets:[dataLow,dataCurr,dataHgh]};
    return IChartData;
}

export function Chart({ISymbolData,tooltip}:any){
    let chrt:any = useRef(null);
    let [data,setData]:any = useState({});
    useEffect(()=>{
        setData(produceIChartData(ISymbolData));
    },[ISymbolData]);
    return (
        <div ref={chrt} style={{position:'absolute',left:'5%',top:'10%',width:'90%',height:'85%',backgroundColor:'white',borderTopRightRadius:'2.5%',borderBottomLeftRadius:'2.5%'}}>
            <Line data={data} options={{maintainAspectRatio:false,tooltips:{enabled:tooltip}}}/>
        </div>
    );   
}