
export default function CreateSearchResults(ISymbolsArray:Array<any>,content:string,setResults:any){
    let tempResults:Array<any> = [];
    if(content.length === 0){
        setResults([]);
        return;
    }
    let contentToSearch = content.toUpperCase();
    let leng = ISymbolsArray.length;
    for(let i=0;i<leng;i++){
        let ISymbolElement = ISymbolsArray[i];
        if(ISymbolElement.ISymbol[0] === contentToSearch[0] || ISymbolElement.description[0] === contentToSearch[0]){
            if(ISymbolElement.ISymbol.includes(contentToSearch) || ISymbolElement.description.includes(contentToSearch)){
                tempResults.push(ISymbolElement);
            }
        }
    }
    setResults(tempResults);
}